.box {
  position: absolute;
  display: flex;
  justify-content: flex-end;
  top: 50vh;
  left: 50vw;
  width: auto;
  padding: 0px;
  transform: translate(-50%, -50%);
  background-color: #202020;
  box-sizing: border-box;
  border-width: 2px;
  box-shadow: 4px 7px 10px rgba(0, 0, 0, 0.4);
  align-content: space-between;
  align-items: center;
}

.box .corouselbutton {
  color: rgb(255, 255, 255);
  background-color: #202020;
  border: #202020;
  padding: 25vh 10px;
  display: inline-block;
  font: poppins;
  font-size: 25px;
  letter-spacing: 1px;
  cursor: pointer;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}

.box .corouselbutton:hover {
  box-shadow: inset 100vh 0 0 0 rgb(53, 52, 52);
}

.contentarea .article {
  vertical-align: center;
  align-items: center;
}

.contentarea h2 {
  font-size: 30px;
  color: white;
  text-align: center;
  animation: appearin 500ms ease-in 0s forwards;
}

.contentarea h2:hover {
  font-size: 40px;
}

.contentarea h3 {
  color: white;
  text-align: center;
  animation: appearin 500ms ease-in 0s forwards;
}
.contentarea h3:hover {
  font-size: 25px;
}
.contentarea p {
  font-size: 16px;
  color: white;
  text-align: center;
  animation: appearin 500ms ease-in 0s forwards;
}

.contentarea p:hover {
  font-size: 20px;
}

@keyframes appearin {
  from {
    color: transparent;
  }
  to {
    color: white;
  }
}
