.nav__cont__close {
  position: absolute;
  width: 60px;
  top: 0;
  height: 100vh;
  z-index: 100;
  background-color: #202020;
  overflow: hidden;
  transition: width 0.3s ease;
  cursor: pointer;
  box-shadow: 4px 7px 10px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.nav__cont__open {
  position: absolute;
  width: 200px;
  top: 0;
  height: 100vh;
  z-index: 100;
  background-color: #202020;
  overflow: hidden;
  transition: width 0.3s ease;
  cursor: pointer;
  box-shadow: 4px 7px 10px rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.nav__cont__close .menu {
  margin: 0;
  position: relative;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.nav__cont__close .menu p {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  font-size: 20px;
  font-weight: bold;
  font: poppins;
  color: white;
}

.nav__cont__open .menu {
  margin: 0;
  position: relative;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.nav__cont__open .menu p {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  font-size: 20px;
  font: poppins;
  color: white;
}

.nav {
  position: relative;
  top: 50vh;
  transform: translateY(-50%);
  list-style-type: none;
  color: white;
}
.nav__items {
  padding-bottom: 4rem;
}
.nav__items .navbtn {
  position: relative;
  display: block;
  top: -35px;
  padding-left: 12.5px;
  padding-right: 15px;
  transition: all 0.3s ease;
  margin-left: 30px;
  margin-right: 10px;
  text-decoration: none;
  color: white;
  font: poppins;
  font-size: 14;
}
.nav__items .navbtn:after {
  content: "";
  width: 100%;
  height: 150%;
  position: absolute;
  top: -25%;
  left: 0;
  border-radius: 2px;
  background: radial-gradient(
    circle at 94.02% 88.03%,
    #797d81,
    transparent 100%
  );
  opacity: 0;
  transition: all 0.5s ease;
  z-index: -10;
}
@media screen and (min-width: 600px) {
  .nav__cont {
    width: 80px;
  }
  .nav__cont:hover {
    width: 200px;
  }
  .nav__cont .menu p {
    font-size: 25px;
  }
  .nav__items .navbtn {
    font-size: 1.35em;
    padding-left: 25px;
  }
}
.nav__items:hover a:after {
  opacity: 1;
}
