.homebox {
  position: absolute;
}

.textname {
  position: relative;
  top: 50vh;
  left: 53vw;
  width: 60vw;
  height: auto;
  transform: translate(-50%, -50%);
  animation: fill-white 1s ease forwards 3s;
}

.textname::before {
  content: "Hello I am";
  color: white;
  font: poppins;
  font-size: 20px;
}
