.contactdetails {
  position: absolute;
  top: 125px;
  left: 50vw;
  text-align: center;
  width: 450px;
  padding: 40px;
  transform: translate(-50%, -50%);
}
.contactdetails a:link {
  position: relative;
  font: poppins;
  background-color: #efefef;
  color: #000;
  padding: 10px 20px;
  display: inline-block;
  margin-top: 25px;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}
.contactdetails a:hover {
  color: #fff;
  box-shadow: inset 0 100px 0 0 black;
}
.contactdetails a:active {
  color: black;
  box-shadow: inset 0 100px 0 0 white;
}
.contactform {
  position: absolute;
  top: 420px;
  left: 50vw;
  width: 450px;
  padding: 40px;
  transform: translate(-50%, -50%);
  background: #efefef;
  box-sizing: border-box;
  border-width: 2px;
  border-color: grey;
}

.contactdetails a {
  font: poppins;
  font-weight: normal;
  text-decoration: unset;
  color: white;
}

.button {
  color: #000;
  border: 2px solid black;
  border-radius: 0px;
  padding: 1.5vh 1.5vw;
  display: inline-block;
  font: poppins;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
  margin: 12px 0;
}

.button:hover {
  color: #fff;
  box-shadow: inset 0 100px 0 0 black;
}

.button:active {
  color: black;
  box-shadow: inset 0 100px 0 0 white;
}

.contactform .inputblock1,
.contactform .inputblock2 {
  margin: 12px 0;
  width: 100%;
  padding: 12px 20px;
  background-color: #efefef;
  border: none;
  border-bottom: 2px solid black;
  resize: none;
  box-sizing: border-box;
}
.contactform .inputblock3 {
  margin: 25px 0;
  width: 100%;
  height: auto;
  padding: 12px 20px;
  background-color: #efefef;
  border: 2px solid black;
  resize: vertical;
  box-sizing: border-box;
}

.contactform .inputblock1 :focus,
.contactform .inputblock2 :focus {
  outline: none;
  border-bottom: 10px solid black;
}

@media screen and (max-width: 600px) {
  .contactform {
    width: 350px;
  }
}

.loadingmessage {
  position: absolute;
  left: 50vw;
  top: 30px;
  width: auto;
  transform: translate(-50%, -50%);
  padding-left: 15px;
  padding-right: 15px;
  background: #efefef;
  box-sizing: border-box;
  animation: load-anime 0.5s linear forwards;
}
.loadingmessage p {
  color: black;
  font: poppins;
}

@keyframes load-anime {
  from {
    left: 50vw;
    top: -200px;
  }
  to {
    left: 50vw;
    top: 30px;
  }
}
