.mainsection {
  position: absolute;
  width: 100%;
  animation: landing-effect 2s ease;
}
.container {
  display: flex;
  position: relative;
  top: 15vh;
}
.container .game {
  margin: auto auto;
  text-align: center;
  line-height: 70px;
  width: 348px;
  height: 348px;
}
.container .game .cell {
  width: 70px;
  height: 70px;
  margin-right: 7px;
  border-radius: 4px;
  margin-bottom: 7px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.2) 0 1px 2px 0;
  color: #333;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease-in;
  position: absolute;
}
.container .empty {
  opacity: 0;
}
.instructions {
  position: relative;
  margin-top: 17.5vh;
  text-align: center;
  font: poppins;
  font-size: 2.5vmax;
  padding-bottom: 0;
}
.instructions p {
  padding: 0;
  margin: 0;
}
.instruction1 {
  animation: color-change 3s infinite 0.3s;
}
.instruction2 {
  animation: color-change 3s infinite 0.6s;
}
.instruction3 {
  animation: color-change 3s infinite 0.9s;
}
@keyframes color-change {
  0% {
    color: rgb(95, 95, 95);
  }
  20% {
    color: grey;
  }
  40% {
    color: white;
  }
  60% {
    color: white;
  }
  80% {
    color: grey;
  }
  100% {
    color: rgb(95, 95, 95);
  }
}

@keyframes landing-effect {
  from {
    top: -750px;
  }
  to {
    top: 0;
  }
}
