.skillcontainer {
  position: absolute;
  top: 50vh;
  left: 50vw;
  width: 50vw;
  transform: translate(-50%, -50%);
}

.skills {
  display: grid;
  grid-gap: 5px;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 70px);
  margin: 20px;
}
.skl {
  font: poppins;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  color: rgb(255, 255, 255);
}
.sklLine {
  position: relative;
  bottom: 8px;
  visibility: hidden;
  padding-left: 6px;
  color: white;
}
.ProgWidth {
  padding: 0px 20px;
}
.skillDiv {
  height: 10px;
  width: 100%;
  background-color: black;
}
.skillBar {
  display: block;
  height: 10px;
  background-color: rgb(255, 255, 255);
}
.p {
  color: white;
  display: flex;
  justify-content: space-between;
}

.htmlBar {
  width: 50%;
  animation: html 3s 0s 1 forwards;
}
.cssBar {
  width: 50%;
  animation: css 3s 0s 1 forwards;
}
.jsBar {
  width: 50%;
  animation: js 3s 0s 1 forwards;
}

.javaBar {
  width: 85%;
  animation: java 3s 0s 1 forwards;
}
.springBar {
  width: 65%;
  animation: spring 3s 0s 1 forwards;
}
.reactBar {
  width: 70%;
  animation: react 3s 0s 1 forwards;
}
.vbaBar {
  width: 75%;
  animation: vba 3s 0s 1 forwards;
}
.sqlBar {
  width: 60%;
  animation: sql 3s 0s 1 forwards;
}

@keyframes html {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}
@keyframes css {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}
@keyframes js {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}
@keyframes java {
  0% {
    width: 0%;
  }
  100% {
    width: 85%;
  }
}
@keyframes spring {
  0% {
    width: 0%;
  }
  100% {
    width: 65%;
  }
}
@keyframes react {
  0% {
    width: 0%;
  }
  100% {
    width: 70%;
  }
}
@keyframes vba {
  0% {
    width: 0%;
  }
  100% {
    width: 75%;
  }
}

@keyframes sql {
  0% {
    width: 0%;
  }
  100% {
    width: 60%;
  }
}
