body {
  height: 100vh;
  display: flex;
  background: radial-gradient(
      circle at 94.02% 88.03%,
      #000000,
      transparent 100%
    ),
    radial-gradient(circle at 25.99% 27.79%, #292929, transparent 100%),
    radial-gradient(circle at 50% 50%, #000000, #000000 100%);
}

.mediasection {
  display: flex;
  position: absolute;
  top: 5%;
  right: 5%;
  gap: 1vw;
  z-index: 2;
}

button {
  color: #000;
  border: 2px solid white;
  border-radius: 0px;
  padding: 1.5vh 1.5vw;
  display: inline-block;
  font: poppins;
  font-size: 14px;
  letter-spacing: 1px;
  cursor: pointer;
  -webkit-transition: ease-out 0.4s;
  -moz-transition: ease-out 0.4s;
  transition: ease-out 0.4s;
}

button:hover {
  color: #fff;
  box-shadow: inset 0 100px 0 0 black;
}
